    width: 40px;
    height: 40px;
    position: absolute;
    left: -40px;
    top: 70px;
    z-index: 2;
    background: rgba(241, 241, 241, 0.576);
    backdrop-filter: blur(4.8px);
    border-radius: 16px 0 0 16px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.fa-xmark {
    position: absolute;
    top: 25px;
    right: 25px;
    font-size: 1.8rem;
    cursor: pointer;
}

aside i {
    font-size: 1.2rem;
}

form {
    
    background-color: rgba(224, 224, 224, 0.577);
    display: inline-flex;
    justify-content: space-between;
    width: 70%;
    gap: 10px;
    padding: 8px 15px;
    border-radius: 16px;
    box-shadow: -1px 1px 25px -20px rgba(55,55,55,0.75);
}



input {
    width: 100%;
    background: none;
    border: none;
}

input:focus {
    outline: none;
}

#sidebar-sections-wrapper {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

h3 {
    border-bottom: 2px solid rgb(55, 55, 55);
    margin-bottom: 20px;
    font-size: 1.3rem;
}

#seven-day-forecast-wrapper > div {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.day-forecast-wrapper div > div:nth-child(1) {
    width: 111px;
}

.day-forecast-wrapper div > div:nth-child(2) {
    width: 135px;
}

.day-forecast-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 500;
    padding: 3px 8px;
    border: none;
    border-radius: 16px;
}

.day-forecast-wrapper > div {
    display: flex;
    gap: 40px;
    align-items: center;
}

.day-forecast-wrapper > div > div {
    display: flex;
    gap: 10px;
    align-items: center;
}


.day-forecast-wrapper img {
    height: 19px;
}


.todays-forecast , #hourly-cards-wrapper .current-hour {
    background-color: rgba(255, 255, 0, 0.408);
}

.high-temperature {
    color: rgb(255, 28, 28);
}

.low-temperature {
    color: rgb(6, 101, 255);
}

#hourly-cards-wrapper {
    display: flex;
    gap: 20px;
    overflow-x: auto;
}

#current-condition-cards-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;

}

#hourly-cards-wrapper div {
    background-color: rgba(224, 224, 224, 0.577);
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    padding: 15px 30px;
    border: 1px solid rgba(171, 171, 171, 0.322);
    border-radius: 16px;
    box-shadow: -1px 1px 25px -20px rgba(55,55,55,0.75);
    font-weight: 500;
}



#current-condition-cards-wrapper div {
    background-color: rgba(224, 224, 224, 0.577);
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    padding: 10px 30px;
    border: 1px solid rgba(171, 171, 171, 0.322);
    border-radius: 16px;
    box-shadow: -1px 1px 25px -20px rgba(55,55,55,0.75);
    font-weight: 500;
} 



#hourly-cards-wrapper img {
    width: 25px;
}

#current-condition-cards-wrapper img {
    width: 25px;
}


